.project {
    /* border: 1px solid red; */
    margin-top: 100px;
    padding: 10px 10%;
}

.projectsContainer {
    /* border: 1px solid purple; */
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
}

.projectsContainer>div {
    /* border: 1px solid blue; */
    border-radius: 5px;
    border-top-left-radius: 20px;
    border-bottom-right-radius: 20px;
    padding: 10px;
}

.projectsContainer>div>img {
    border-top-left-radius: 20px;
    width: 100%;
}

.projectsContainer>div>div img {
    width: 30px;
}

.projextLinks img{
    border-radius: 50%;
}

.projectsContainer>div>div img {
    width: 30px;
}

.tekstackLive div {
    /* border: 1px solid red; */
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5%;
    margin: 7px 0px;
}
.tekstackLive div:last-child{
    margin-top: 20px;
}

.projectsContainer>div h2 {
    text-align: center;
    margin: 0px;
    font-size: 40px;
    margin-bottom: -20px;
}
.projectsContainer>div p{
    font-weight: 400;
}

.projectsContainer>div a {
    text-decoration: none;
}

.projectsContainer>div {
    background: linear-gradient(151deg, rgba(64,66,221,1) 4%, rgba(17,188,244,0.7011846975118172) 97%);
}


.projectsContainer>div:nth-child(4) {
    background: none;
    display: flex;
    justify-content: center;
    align-items: center;
}

.projectsContainer>div span {
    border: 1.5px solid black;
    padding: 5px 10px;
}

@media(max-width:767px) {
    .projectsContainer {
        grid-template-columns: repeat(1, 1fr);
    }
}
@media (max-width:480px){
    .project{
        margin-top: 0px;
    }
    .projectsContainer>div {
        border-radius: 5px;
        border-top-left-radius: 15px;
        border-bottom-right-radius: 15px;
    }
    .projectsContainer>div>img {
        border-top-left-radius: 10px;
    }
}