.about {
    /* border: 1px solid red; */
    margin: 150px 0px;
    font-size: 19px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 150px 0px;
}

.about div {
    border: 2px solid #3a50e0;
    width: 80%;
    padding: 1%;
    z-index: 4;
    border-radius: 7px;
    border-top-left-radius: 30px;
    border-bottom-right-radius: 30px;
}

.about img {
    height: 500px;
    width: 100%;
    position: absolute;
    opacity: 0.7;
}


@media(max-width:480px) {
    .about {
        font-size: 16px;
    }
}
